<template>
    <div>
        <div style="margin: 20px">
            <el-input-number style="width: 100%" v-model:value="intValue"/>
            <div></div>
            <el-button @click="exchangeToDate">转换</el-button>
            <div></div>
            <el-input v-model:value="formatIntValue"/>
        </div>
        <div style="margin: 20px">
            <el-date-picker
                    v-model="dateValue"
                    type="datetime"
                    placeholder="选择日期">
            </el-date-picker>
            <div></div>
            <el-button @click="exchangeToInt">转换</el-button>
            <div></div>
            <el-input v-model:value="formatDateValue"/>
            <div></div>
            <el-input v-model:value="formatDateValueAll"/>
        </div>

    </div>
</template>

<script>


    export default {
        name: "date_tool",
        // 引用组件
        components: {},
        data() {
            return {
                dateFormat: "yyyy-MM-DD HH:mm:ss",
                intValue: 0,
                formatIntValue: "",
                dateValue: null,
                formatDateValue: 0,
                formatDateValueAll: 0,
            }
        },
        mounted() {
            this.init();
        },
        methods: {

            // 初始化数据
            init() {

            },

            exchangeToDate() {
                let _that = this
                const timestamp = "" + _that.intValue
                if (timestamp.length === 13) {
                    _that.formatIntValue = new Date(parseInt(timestamp)).toLocaleString();
                } else if (timestamp.length === 10) {
                    _that.formatIntValue = new Date(parseInt(timestamp) * 1000).toLocaleString();
                } else {
                    alert("当前时间戳有问题")
                }


            },
            exchangeToInt() {
                let _that = this

                let timestamp = Date.parse(_that.dateValue);
                console.log(timestamp)
                _that.formatDateValueAll = timestamp
                _that.formatDateValue = _that.formatDateValueAll / 1000
            }

        },
    }
</script>

