<template>
    <div>
        <h1>This is an dash page</h1>
        <div>{{count}}</div>
        <el-button @click="button1">+</el-button>
    </div>
</template>

<script>
    export default {
        name: "dash",
        data() {
            return {
                count: 0,
            }
        },
        methods: {
            button1() {
                let _that = this
                _that.count++
                let mes = "" + _that.count
                ELEMENT.Message.info(mes)
            }
        }
    }
</script>

